.feedback {
  position: relative;
  min-height: 840px;
  background: $color-deep-sky-blue;
  z-index: 0;

  @media #{$wide-vp} {
    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: $color-transparent-black-perl;
      clip-path: polygon(0 0, calc(50% + 12px) 0, calc(50% - 961px) 100%, 0 100%);
      z-index: -1;
    }
  }

  @media #{$middle-vp} {
    min-height: 580px;
  }

  @media #{$narrow-vp} {
    min-height: 750px;
  }
}

.feedback img {
  position: absolute;
  object-fit: contain;
  top: 246px;
  left: 30px;

  @media (max-width: 1200px) {
    left: calc(30px + (100vw - 1200px));
  }

  @media #{$not-wide-vp} {
    display: none;
  }
}
